import React from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import img_down from "../svg/arrow_down_b.svg";
import { actionCreators as userActions } from "../redux/modules/user";
import Swal from "sweetalert2";

function Signup(props) {
  const dispatch = useDispatch();
  const [username, setUsername] = React.useState("");
  const [nickname, setNickname] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [checkPassword, setCheckPassword] = React.useState("");
  const [career, setCareer] = React.useState("");
  const [careerSelect, setCareerSelect] = React.useState("");

  //// 
  const [idRuleCheck, setIdRuleCheck] = React.useState(false);
  const [passwordRuleCheck, setPasswordRuleCheck] = React.useState(false);
  const [nicknameRuleCheck, setNicknameRuleCheck] = React.useState(false);

  ////// 
  const [isCheckUsername, setIsCheckUsername] = React.useState(false);
  const [isCheckNickname, setIsCheckNickname] = React.useState(false);

  /// 
  const options = [
    { value: "1 ", name: "1 " },
    { value: "1~2", name: "1~2" },
    { value: "3~4", name: "3~4" },
    { value: "5 ", name: "5 " },
  ];

  ///////////////////////////////// 
  const idRule = (e) => {
    const rule =
      /^(((?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{4,10})|([A-Za-z]{4,10}))$/;
    const id = e.target.value;
    setUsername(id);
    rule.test(id) ? setIdRuleCheck(true) : setIdRuleCheck(false);
  };

  const nicknameRule = (e) => {
    const rule = /^[-|-|-A-Za-z0-9]{2,8}$/;
    const nick = e.target.value;
    setNickname(nick);
    rule.test(nick) ? setNicknameRuleCheck(true) : setNicknameRuleCheck(false);
  };

  const passwordRule = (e) => {
    const rule = /^(?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{4,10}$/;
    const password = e.target.value;
    setPassword(password);
    rule.test(password)
      ? setPasswordRuleCheck(true)
      : setPasswordRuleCheck(false);
  };

  ///////////////////////////////////// 

  const checkUsername = () => {
    if (username === "") {
      Swal.fire("", "  .", "error");
      return;
    } else if (idRuleCheck === false) {
      Swal.fire("", "  .", "error");
      return;
    }
    console.log(username, "");
    dispatch(userActions.checkUsernameDB(username, false));
    setIsCheckUsername(true);
  };

  const checkNickname = () => {
    if (nickname === "") {
      Swal.fire("", " .", "error");
      return;
    } else if (nicknameRuleCheck === false) {
      Swal.fire("", "  .", "error");
      return;
    }
    console.log(nickname, "   dispatch .");
    dispatch(userActions.checkNicknameDB(nickname, false));
    setIsCheckNickname(true);
  };

  ///////////////////////
  const signup = (e) => {
    e.preventDefault();

    setIsCheckNickname(true);
    setIsCheckUsername(true);
    console.log(isCheckUsername, isCheckNickname);
    if (username === "" || nickname === "" || password === "") {
      Swal.fire("", " .", "error");
      return;
    } else if (passwordRuleCheck !== true) {
      Swal.fire("", "   .", "error");
      return;
    } else if (password !== checkPassword) {
      Swal.fire("", " .", "error");
      return;
    } else if (isCheckUsername === false || isCheckNickname === false) {
      Swal.fire("", " .", "error");
      return;
    } else if (career === "") {
      Swal.fire("", "  .", "error");
      return;
    }
    dispatch(
      userActions.signupDB(username, nickname, password, checkPassword, career)
    );
    setIsCheckUsername(false);
    setIsCheckNickname(false);
  };
  return (
    <Container>
      <div>
        <div>
          <span className='inputSpan'></span>
          <input
            type='text'
            placeholder='  4  10  .'
            onChange={idRule}
          />
          <button className='CheckButton' onClick={checkUsername}>
            
          </button>
        </div>
        {username.length > 0 && !idRuleCheck && (
          <span className='rule'> *   . </span>
        )}
      </div>

      <div>
        <div>
          <span style={{ width: "58px" }} className='inputSpan'>
            
          </span>
          <input
            type='password'
            placeholder='password'
            onChange={passwordRule}
          />
        </div>
        {password.length > 0 && !passwordRuleCheck && (
          <span className='rule'> *   4  . </span>
        )}
      </div>

      <div>
        <div>
          <span style={{ width: "100px" }} className='inputSpan'>
             
          </span>
          <input
            type='password'
            onChange={(e) => {
              setCheckPassword(e.target.value);
            }}
          />
        </div>{" "}
        {checkPassword !== password && (
          <span className='rule'> * . </span>
        )}
      </div>

      <div>
        <div>
          <span className='inputSpan'></span>
          <input type='text' placeholder='Nickname' onChange={nicknameRule} />
          <button className='CheckButton' onClick={checkNickname}>
            
          </button>
        </div>
        {nickname.length > 0 && !nicknameRuleCheck && (
          <span className='rule'> *   . </span>
        )}
      </div>

      <Career>
        <div>
          <span onClick={() => setCareerSelect(!careerSelect)}>{career}</span>
          {careerSelect === true ? (
            <ul>
              {options.map((v, idx) => (
                <li
                  key={idx}
                  onClick={() => {
                    setCareer(v.name);
                    setCareerSelect(false);
                  }}>
                  {v.name}
                </li>
              ))}
            </ul>
          ) : null}
        </div>
        <img
          onClick={() => setCareerSelect(!careerSelect)}
          src={img_down}></img>
        {career === "" && (
          <span className='rule'> *  </span>
        )}
      </Career>
      <div>
        <button className='SignupButtom' onClick={signup}>
          
        </button>
      </div>
    </Container>
  );
}

const Container = styled.div`
  width: 500px;
  height: 472px;
  margin: 0 auto;
  text-align: center;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  div {
    width: 100%;
    height: 72px;
    display: flex;
    flex-direction: column-reverse;
    > span.rule {
      color: red;
      font-size: 12px;
      position: absolute;
      justify-content: start;
      padding-left: 15px;
    }
    > div {
      width: 100%;
      height: 72px;
      display: flex;
      flex-direction: row;
      position: relative;
      justify-content: end;
      align-items: center;
      background-color: #f9f8ff;
      border-radius: 0.5rem;
      padding: 15px;
      > span.inputSpan {
        color: #7966ff;
        font-size: 16px;
        font-weight: 800;
      }
    }
  }
  input {
    width: 90%;
    height: 32px;
    border: 0px solid black;
    font-size: 16px;
  }
  button.CheckButton {
    width: 76px;
    height: 32px;
    background-color: #7966ff;
    color: white;
    border-radius: 32px;
    position: absolute;
  }
  button.SignupButtom {
    width: 100%;
    height: 72px;
    background-color: #5e45f2;
    color: white;
    border-radius: 0.5rem;
  }
`;
const Career = styled.div`
  width: 500px;
  display: flex;
  position: relative;
  justify-content: end;

  div {
    width: 100%;
    display: flex;
    flex-direction: column;
    > span {
      width: 100%;
      height: 72px;
      line-height: 72px;
      padding-left: 5px;
      border-radius: 8px;
      background-color: #f9f8ff;
      cursor: pointer;
      color: #7966ff;
      font-size: 16px;
      font-weight: 800;
      text-align: start;
    }
    > ul {
      width: 100%;
      height: 72px;
      border-radius: 0.5rem;
      border: 0px;
      position: absolute;
      margin-top: 150px;
      > li {
        display: flex;
        width: 100%;
        height: 72px;
        margin-left: 15px;
        font-size: 16px;
        font-weight: 600;
        padding-left: 10px;
        background-color: #f9f8ff;
        align-items: center;
        justify-content: start;
        cursor: pointer;
        &:hover {
          background-color: #5e45f2;
          color: #f9f8ff;
        }
      }
    }
  }
  > img {
    width: 30px;
    height: 30px;
    display: flex;
    position: absolute;
    margin: 0 0 20px 450px;
    cursor: pointer;
  }
`;
export default Signup;
